.box-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 621px;
    margin: auto;
    margin-bottom: 30px;

    .box-header__title {
        color: #2E2A41;
    }

    .box-header__text {
        font-size: 16px;
        text-align: center;
    }
}

.image {
    position: relative;
    height: 614px;

    .image__bg {
        position: absolute;
        top: 0;
        width: 75%;
        height: 100%;
        background-color: #F0F9FE;
        border-radius: 0 40px 40px 0;
        z-index: -1;
    }

    .image__product-image {
        position: relative;
        max-width: 100%;
        top: 50%;
        left: 85px;
        transform: translateY(-50%);
        box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.1);
    }
}