.header {
    width: 100%;
}

.container {
    padding: 22px 0;
}

.header-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-wrap__logo {
    width: 137px;
    height: 66px;
}

.header-wrap__menu {
    display: flex;
    gap: 37px;
}

.menu-item {
    display: flex;
    align-items: center;
    gap: 50px;
}

.menu-item__item {
    font-size: 15px;
    color: #4F4B65;
}

.menu-button {
    display: flex;
    gap: 15px;
}