.text-box {
    position: relative;

    &:before {
        content: "";
        position: absolute;
        width: 79px;
        height: 79px;
        border-radius: 50%;
        top: -25px;
        left: -40px;
        z-index: -1;
    }
}

.text-box__icon {
    width: 92px;
    height: 92px;
    border-radius: 50%;
    background-color: #FDF1E7;
    display: flex;
    align-items: center;
    justify-content: center;

    i {
        font-size: 50px;
    }
}

.text-box__comment {
    font-size: 16px;
    padding: 20px 0 36px 0;
}

.text-box__dot {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 59px;

    .dot {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: #E8E8EB;
    }

    .dot-1 {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color: #5E68F9;
    }
}

.text-box--pink:before {
    background-color: #FBE9E9;
}

.text-box--mint:before {
    background-color: #E7FAF9;
}

.text-box--purple:before {
    background-color: #F5ECFD;
}

.text-box--blue:before {
    background-color: #FBE9E9;
}

.text-box-style2 {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.text-box__author {
    display: flex;
    align-items: center;
    gap: 10px;
}

.author__cross {
    width: 19px;
    border: 1px solid #D8D8D8;
}

.author__name {
    font-size: 14px;
    color: #2E2A41;
}