.detail {
    position: relative;
    border-radius: 6px;
}

.detail__image {
    width: 100%;
    position: relative;
}

.image {
    width: 100%;
}

.detail__text {
    padding: 15px;
}

.text__title {
    font-weight: 500;
    color: #2E2A41;
}

.text__small-text {
    font-size: 12px;
    padding: 5px 0;
}

.decor {
    display: flex;
    position: absolute;
    bottom: -5px;
}

.decor__front {
    width: 75px;
    background-color: #5E68F9;
    color: #FFFFFF;
    font-size: 13px;
    text-align: center;
    padding: 5px 0;
}

.decor__back {
    width: 0;
    height: 0;
    border-bottom: 19px solid rgb(57, 63, 150);
    border-right: 12px solid transparent;
}